.calendar {
  width: 17.5rem;
  color: white;
  border-radius: 0.3rem;
  user-select: none; }
  .calendar__date {
    width: 100%;
    height: 5.625rem;
    background-color: #e75735;
    font-weight: 300;
    font-size: 5rem;
    text-align: center;
    padding-top: 1rem;
    border-radius: 0.3rem 0.3rem 0 0; }
  .calendar .ui-widget.ui-widget-content {
    padding: 0;
    border: none;
    border-radius: 0;
    width: 100%; }
  .calendar .ui-datepicker .ui-datepicker-header {
    display: flex;
    width: 100%;
    height: 2.5rem;
    background-color: #d54c2c;
    text-align: center;
    align-items: center;
    font-weight: normal;
    font-size: 1.5rem;
    border: none;
    border-radius: 0;
    padding: 0;
    color: white; }
    .calendar .ui-datepicker .ui-datepicker-header span.ui-datepicker-month {
      font-family: 'Conv_Lato';
      font-weight: 300;
      font-size: 1.5rem; }
    .calendar .ui-datepicker .ui-datepicker-header span.ui-datepicker-year {
      display: none; }
    .calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-title {
      flex: 4; }
    .calendar .ui-datepicker .ui-datepicker-header a > span {
      display: none; }
    .calendar .ui-datepicker .ui-datepicker-header .ui-state-hover {
      background: none;
      border: none; }
    .calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-prev-hover {
      position: absolute;
      top: 0.125rem;
      left: 0.125rem;
      width: 1.8em;
      height: 1.8em; }
    .calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-next-hover {
      position: absolute;
      top: 0.125rem;
      right: 0.125rem;
      width: 1.8em;
      height: 1.8em; }
    .calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-prev {
      top: 0;
      height: 2.5rem;
      background: url(img/arrow.png) 50% 50% no-repeat;
      background-size: 1.125rem;
      cursor: pointer; }
    .calendar .ui-datepicker .ui-datepicker-header .ui-datepicker-next {
      top: 0;
      height: 2.5rem;
      background: url(img/arrow.png) 50% 50% no-repeat;
      background-size: 1.125rem;
      cursor: pointer;
      transform: rotate(180deg); }
  .calendar .ui-datepicker table {
    border-spacing: 0;
    margin: 0; }
    .calendar .ui-datepicker table thead {
      height: 1.8rem;
      background-color: #e75735;
      text-align: center;
      justify-content: space-around;
      align-items: center;
      border-bottom: 0.06rem solid white; }
      .calendar .ui-datepicker table thead span {
        font-family: 'Conv_Lato';
        color: white;
        font-weight: normal;
        font-size: 0.625rem;
        text-transform: uppercase; }
    .calendar .ui-datepicker table .ui-state-default {
      border: none;
      text-align: center;
      font-family: 'Conv_Lato';
      color: #747474;
      font-weight: normal;
      font-size: 1.125rem;
      background-color: #f2f2f2;
      height: 1.8rem;
      padding: 0;
      padding-top: 0.44rem; }
    .calendar .ui-datepicker table .ui-state-active {
      color: white;
      background-color: #e75735;
      opacity: 1; }
    .calendar .ui-datepicker table .ui-state-disabled {
      background-color: #f2f2f2;
      opacity: 1; }
      .calendar .ui-datepicker table .ui-state-disabled span {
        color: white; }
    .calendar .ui-datepicker table td {
      width: 2.5rem;
      background-color: #f2f2f2;
      border: 0.125rem solid white;
      border-collapse: collapse; }
  .calendar .ui-datepicker .ui-datepicker-buttonpane {
    display: block;
    width: 17.5rem;
    height: 3.125rem;
    background-color: #e5e5e5;
    color: #747474;
    font-weight: bold;
    font-size: 0.88rem;
    text-transform: uppercase;
    padding-top: 1.125rem;
    padding-left: 1.375rem;
    border-radius: 0 0 0.3rem 0.3rem;
    margin: 0;
    padding: 0; }
    .calendar .ui-datepicker .ui-datepicker-buttonpane button.ui-datepicker-current {
      background-color: #e5e5e5;
      font-weight: bold;
      font-size: 0.88rem;
      text-transform: uppercase;
      padding-top: 0.625rem;
      padding-left: 1.25rem;
      border: none;
      outline: none; }
