.news-page {
  width: 95%;
  font-family: 'Conv_Lato';
  font-weight: normal;
  font-size: 1rem;
  margin: 0 auto;
  padding-bottom: 3.125rem;
  height: auto; }
  .news-page__wrapper {
    margin-top: 3.125rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around; }
  .news-page_left {
    flex: 2;
    margin: 0 auto; }
  .news-page_right {
    flex: 1;
    margin: 0 auto; }
  .news-page__content {
    max-width: 42.5rem;
    margin: 0 auto;
    padding: 1.56rem;
    padding-bottom: 1.875rem;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 0.06rem solid #bbbbbb;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
    .news-page__content .event, .news-page__content .news {
      margin: 0.625rem; }
  .news-page__left-block {
    width: 18.75rem;
    margin: 0 auto;
    padding: 1.56rem;
    padding-bottom: 1.875rem;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 0.06rem solid #bbbbbb;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .news-page__item {
    padding: 0.3125rem 0.3125rem; }
