.btn {
  position: relative;
  width: inherit;
  height: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: none;
  border-radius: inherit;
  background-color: white;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  overflow: hidden; }
  .btn_theme_red {
    box-shadow: 0 0.2rem #bf3e1f;
    border: 0.06rem solid #bf3e1f;
    color: #e75735;
    background-color: white; }
    .btn_theme_red.btn_small {
      box-shadow: 0 0.125rem #bf3e1f; }
    .btn_theme_red:hover {
      background-color: #e75735;
      color: white; }
    .btn_theme_red:active {
      transform: translateY(0.2rem);
      box-shadow: 0 0;
      color: white;
      background-color: #e75735; }
  .btn_theme_green {
    box-shadow: 0 0.2rem #29a391;
    border: 0.06rem solid #29a391;
    color: #4eb7a8;
    background-color: white; }
    .btn_theme_green.btn_small {
      box-shadow: 0 0.125rem #29a391; }
    .btn_theme_green:hover {
      color: white;
      background-color: #4eb7a8; }
    .btn_theme_green:active {
      transform: translateY(0.2rem);
      box-shadow: 0 0;
      color: white;
      background-color: #4eb7a8; }

/*  Ripple */
.ripple {
  position: absolute;
  background: #fff;
  border-radius: 50%;
  width: 0.3125rem;
  height: 0.3125rem;
  animation: rippleEffect .88s 1;
  opacity: 0; }

@keyframes rippleEffect {
  0% {
    transform: scale(1);
    opacity: 0.4; }
  100% {
    transform: scale(100);
    opacity: 0; } }
