.main-page {
  width: 95%;
  font-family: 'Conv_Lato';
  font-weight: normal;
  font-size: 1rem;
  margin: 0 auto;
  padding-bottom: 3.125rem;
  height: auto; }
  .main-page__wrapper {
    margin-top: 3.125rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%; }
  .main-page_left {
    flex: 2;
    margin: 0 auto; }
  .main-page_right {
    flex: 1;
    margin: 0 auto; }
  .main-page__video {
    flex: 1;
    margin: 0 auto;
    padding-top: 1.25rem;
    padding-bottom: 1.875rem;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 0.06rem solid #bbbbbb;
    justify-content: center; }
    .main-page__video .video {
      display: flex;
      width: 37.5rem;
      transition: all 0.2s ease-in-out; }
      @media screen and (max-width: 42.5rem) {
        .main-page__video .video {
          transform: scale(0.7);
          height: 18.75rem;
          width: 28.125rem; } }
  .main-page__left-block {
    width: 18.75rem;
    margin: 0 auto;
    padding: 1.56rem;
    padding-bottom: 1.875rem;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 0.06rem solid #bbbbbb;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .main-page__item {
    padding: 0.3125rem 0; }
  .main-page__video-text {
    display: block;
    width: 90%;
    margin: 0 auto;
    margin-top: 1.25rem;
    text-align: justify; }
