.dropdown {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: flex-end; }
  .dropdown__input {
    display: block;
    margin: 0.25rem 0;
    flex: 1;
    height: 1.875rem;
    border-radius: 0.2rem 0 0 0.2rem;
    border: 0;
    background-color: #e5e5e5;
    color: #747474;
    font-weight: normal;
    font-size: 0.81rem;
    padding-left: 0.75rem;
    outline: none;
    -webkit-appearance: button;
    appearance: button; }
  .dropdown:before {
    content: "";
    position: absolute;
    width: 2.2rem;
    margin: 0.25rem 0;
    height: 1.875rem;
    border-radius: 0 0.2rem 0.2rem 0;
    border: 0;
    color: white;
    font-weight: normal;
    font-size: 1.75rem;
    outline: none;
    text-align: center;
    background-color: #4eb7a8;
    pointer-events: none; }
  .dropdown__button {
    background: none;
    border: none;
    z-index: 1;
    color: white;
    position: absolute;
    right: 0;
    top: 0.35rem;
    pointer-events: none; }
  .dropdown__item {
    display: block;
    font-weight: normal;
    font-size: 0.875rem;
    padding-left: 0.75rem;
    outline: none;
    margin: 0;
    text-decoration-line: none;
    background-color: #4eb7a8;
    color: white;
    padding-top: 0.44rem;
    height: 1.44rem;
    cursor: pointer; }
    .dropdown__item:hover {
      background-color: rgba(255, 255, 255, 0.9);
      color: #4eb7a8; }
