.video-row {
  transition: all 0.2s ease-in-out; }
  .video-row__header {
    padding-top: 2rem;
    margin-bottom: 0.625rem; }
  .video-row__content {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    margin: 0 auto; }
  @media screen and (max-width: 37.5rem) {
    .video-row {
      height: 18.75rem; }
      .video-row__content {
        transform: scale(0.6);
        height: 16.875rem; } }
