.stage {
  width: 100%;
  display: flex;
  font-weight: bold;
  flex-direction: row;
  align-items: center; }
  .stage__circle {
    width: 1.75rem;
    height: 1.75rem;
    background-color: #e5e5e5;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center; }
    .stage__circle_active {
      background-color: #e75735; }
  .stage__text {
    font-size: 0.875rem;
    color: #747474; }
    .stage__text_active {
      color: white; }
  .stage__line {
    flex: 1;
    height: 0.3125rem;
    background-color: #e5e5e5; }
    .stage__line_active {
      background-color: #e75735; }
    .stage__line:first-child {
      flex: 0; }
