.forms {
  min-height: 20.5rem; }
  .forms__header {
    padding-top: 3.4rem;
    margin-bottom: 1.5625rem; }
  .forms__form {
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    justify-content: space-around; }
    .forms__form_left {
      min-width: 18.75rem;
      padding-left: 0;
      padding-bottom: 1.25rem; }
    .forms__form_right {
      padding: 0.25rem 5%;
      width: 14.375rem;
      min-width: 6.25rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 6.125rem 5rem;
      grid-gap: 0.94rem;
      justify-items: center;
      align-items: start; }
  .forms__item {
    list-style-type: none; }

.toggles {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2.1875rem 2.8125rem;
  grid-gap: 0.94rem;
  justify-items: center;
  align-items: start;
  grid-template-areas: "toggle-header toggle-header" "toggle-left toggle-right"; }
  .toggles__header {
    grid-area: toggle-header; }
  .toggles_left {
    grid-area: toggle-left;
    justify-self: right; }
  .toggles_right {
    grid-area: toggle-right;
    justify-self: left; }

.ticks {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2.1875rem 1.875rem;
  grid-gap: 0.94rem;
  grid-column-gap: 1.25rem;
  justify-items: center;
  align-items: start;
  grid-template-areas: "tick-header tick-header"  "tick-left tick-right"; }
  .ticks__header {
    grid-area: tick-header; }
  .ticks_left {
    grid-area: tick-left;
    justify-self: right; }
  .ticks_right {
    grid-area: tick-right;
    justify-self: left; }
