.user-page {
  width: 95%;
  font-family: 'Conv_Lato';
  font-weight: normal;
  font-size: 1rem;
  margin: 0 auto;
  padding-bottom: 3.125rem;
  height: auto; }
  .user-page__wrapper {
    margin-top: 3.125rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around; }
  .user-page_left {
    flex: 2;
    margin: 0 auto;
    min-width: 22.5rem; }
  .user-page_right {
    flex: 1;
    margin: 0 auto; }
  .user-page__forms {
    max-width: 40rem;
    margin: 0 auto;
    padding: 1.875rem;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 0.06rem solid #bbbbbb;
    justify-content: center; }
  .user-page__left-block {
    width: 18.75rem;
    margin: 0 auto;
    padding: 1.56rem;
    padding-bottom: 1.875rem;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 0.06rem solid #bbbbbb;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .user-page__toggles, .user-page__ticks {
    display: block;
    width: 100%;
    margin: 1.25rem;
    font-size: 1.125rem; }
  .user-page__toggle-block {
    display: block;
    width: 100%;
    margin: 0 auto; }
    .user-page__toggle-block span {
      display: inline-block;
      text-align: left;
      margin: 0.3125rem; }
  .user-page__tick-block {
    display: block;
    width: 100%;
    margin: 0 auto; }
    .user-page__tick-block span {
      display: inline-block;
      text-align: left;
      margin: 0.3125rem;
      margin-right: 1.875rem; }
  .user-page__btn {
    text-align: right;
    margin: 0.3125rem; }
