.form-input {
  display: flex; }
  .form-input__text {
    flex: 8;
    margin: 0.3125rem;
    height: 1.875rem;
    border-radius: 0.3125rem;
    border: 0;
    background-color: #e5e5e5;
    color: #747474;
    font-family: inherit;
    font-weight: normal;
    font-size: 0.875rem;
    padding-left: 0.75rem;
    outline: none; }
  .form-input__button {
    position: relative;
    flex: 3;
    margin: 0.3125rem;
    height: 1.875rem;
    border-radius: 0.3125rem;
    border: 0;
    color: white;
    font-family: inherit;
    font-weight: normal;
    font-size: 0.625rem;
    letter-spacing: 0.06rem;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: #4eb7a8; }
    .form-input__button_visible {
      display: flex; }
    .form-input__button_error {
      background-color: #e75735; }
    .form-input__button::before {
      content: "";
      position: absolute;
      background-color: inherit;
      top: 0.75rem;
      left: -0.125rem;
      width: 0.375rem;
      height: 0.375rem;
      transform: rotate(-45deg); }
