.arrow-button {
  cursor: pointer;
  outline: none;
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  border: 0.2rem solid #4eb7a8;
  color: #4eb7a8;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center; }
  .arrow-button .material-icons {
    font-size: 2.625rem;
    align-self: center;
    vertical-align: middle; }
  .arrow-button:hover {
    background-color: #4eb7a8;
    color: white; }
  .arrow-button_disabled {
    cursor: default;
    border: 0;
    background-color: #e5e5e5;
    color: white; }
    .arrow-button_disabled:hover {
      background-color: #e5e5e5;
      color: white; }
