.stat-page {
  width: 95%;
  font-family: 'Conv_Lato';
  font-weight: normal;
  font-size: 1rem;
  margin: 0 auto;
  padding-bottom: 3.125rem;
  height: auto; }
  .stat-page__wrapper {
    margin-top: 3.125rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around; }
  .stat-page_center {
    flex: 1;
    margin: 0 auto;
    min-width: 22.5rem;
    width: 73%;
    margin: 0 auto;
    margin-bottom: 3.125rem;
    background-color: white;
    box-shadow: 0.3125rem 0.625rem 1.56rem #bbbbbb;
    border-radius: 0.3125rem;
    border: 1px solid #bbbbbb;
    text-align: center;
    max-width: 50rem; }
  .stat-page__about {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 90%;
    margin: 6.25rem auto; }
    .stat-page__about .map {
      transition: all 0.2s ease-in-out; }
      @media screen and (max-width: 42.5rem) {
        .stat-page__about .map {
          transform: scale(0.7); } }
  .stat-page__text {
    display: block;
    width: 37.5rem;
    margin: 0 auto;
    margin-top: 1.875rem;
    font-size: 1rem;
    text-align: justify;
    width: 90%; }
