.form-textarea__wrapper {
  padding: 0.3125rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.form-textarea__text {
  height: auto;
  resize: none;
  border-radius: 0.3125rem;
  border: 0;
  background-color: #e5e5e5;
  color: #747474;
  font-family: inherit;
  font-weight: normal;
  font-size: 0.875rem;
  padding: 0.75rem;
  display: block;
  width: 100%;
  outline: none; }

.form-textarea__btn {
  align-self: flex-end;
  justify-self: right;
  margin-top: 0.44rem;
  height: 2rem;
  width: 7.375rem;
  font-size: 0.75rem;
  font-weight: bold;
  border-radius: 0.25rem; }
