.form-toggle .lcs_switch {
  box-shadow: 0 0 0;
  width: 3.75rem;
  height: 1.5rem; }

.form-toggle .lcs_label {
  font-family: 'Conv_Lato';
  font-weight: normal;
  font-size: 0.75rem; }

.form-toggle .lcs_cursor {
  width: 1.125rem;
  height: 1.125rem; }

.form-toggle .lcs_switch.lcs_on .lcs_cursor {
  left: 2.5rem; }

.form-toggle .lcs_switch.lcs_off .lcs_label_off {
  right: 0.3125rem; }

.form-toggle .lcs_switch.lcs_on .lcs_label_on {
  left: 0.44rem; }

.form-toggle .lcs_on {
  background: #4eb7a8; }

.form-toggle .lcs_off {
  background: #e5e5e5; }
