.news {
  display: block;
  width: 18.125rem;
  height: 13.25rem;
  background-color: #e75735;
  border-radius: 0.3125rem;
  font-weight: normal;
  color: white; }
  .news__img {
    display: block;
    float: left;
    width: 5.94rem;
    height: 100%;
    border-radius: 0.3125rem 0 0 0.3125rem; }
  .news__content {
    display: block;
    margin-left: 6.875rem;
    padding: 0.625rem; }
  .news__header {
    margin-top: 0.625rem;
    display: block;
    font-size: 1.625rem;
    font-weight: 300; }
  .news__date {
    display: block;
    margin-top: 0.3125rem;
    font-size: 0.75rem;
    text-transform: uppercase; }
  .news__text {
    display: block;
    margin-top: 0.625rem;
    font-size: 0.75rem; }
