.location {
  transition: all 0.2s ease-in-out; }
  .location__header {
    padding-top: 2.5rem;
    margin-bottom: 1.875rem; }
  .location__content {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    margin: 0 auto; }
  @media screen and (max-width: 37.5rem) {
    .location {
      height: 13.75rem; }
      .location__content {
        transform: scale(0.6);
        height: 11.875rem; } }
