.messaging {
  display: flex;
  width: 18.125rem;
  height: 26.875rem;
  flex-direction: column;
  border-radius: 0.3125rem;
  user-select: none;
  align-items: center;
  text-align: center;
  font-weight: normal;
  color: #747474; }
  .messaging__header {
    width: 100%;
    height: 4.375rem;
    background-color: #4eb7a8;
    font-size: 2rem;
    color: white;
    padding-top: 1.25rem;
    border-radius: 0.3125rem 0.3125rem 0 0;
    font-weight: 300; }
  .messaging__line {
    width: 100%;
    height: 2.1875rem;
    background-color: #29a391; }
  .messaging__icons {
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-between; }
    .messaging__icons i {
      margin-top: 3.44rem;
      z-index: 1000;
      cursor: pointer; }
  .messaging__body {
    width: 100%;
    height: 21.875rem;
    background-color: #f2f2f2;
    border-radius: 0 0 0.3125rem 0.3125rem;
    font-size: 0.875rem; }
  .messaging__message {
    position: relative;
    margin: 0.94rem auto;
    width: 13.125rem;
    height: 2.5rem;
    background-color: white;
    border-radius: 0.3125rem;
    padding: 0.94rem;
    text-align: left;
    user-select: text;
    cursor: text; }
    .messaging__message::before {
      content: "";
      position: absolute;
      width: 0.625rem;
      height: 0.625rem;
      transform: rotate(45deg);
      background-color: white;
      right: 50%;
      top: -0.25rem; }
  .messaging__input {
    margin: 1.06rem auto;
    margin-top: 0.3125rem;
    background-color: #e5e5e5;
    border: 0.1875rem solid white;
    border-radius: 0.3125rem;
    padding: 0.94rem;
    cursor: text;
    resize: none; }
  .messaging__btn {
    width: 15.25rem;
    height: 2.1875rem;
    font-family: 'Conv_Lato';
    font-weight: bold;
    font-size: 0.75rem;
    letter-spacing: 0.06rem;
    border-radius: 0.25rem;
    margin: 0 auto; }
  .messaging__user-photo {
    border-radius: 50%;
    border: 0.1875rem solid #e5e5e5;
    margin-top: -2.8125rem; }
