.user-card {
  display: block;
  width: 16.875rem;
  height: 5.94rem; }
  .user-card__photo {
    display: block;
    float: left;
    border-radius: 50%;
    border: 0.3125rem solid #e5e5e5;
    margin: 0; }
  .user-card__panel {
    display: block;
    float: right;
    width: 10.3125rem;
    height: 5.94rem;
    background-color: #e5e5e5;
    border-radius: 0.3125rem;
    position: relative;
    text-align: center; }
    .user-card__panel::before {
      content: "";
      position: absolute;
      width: 0.625rem;
      height: 0.625rem;
      background-color: #e5e5e5;
      transform: rotate(45deg);
      top: 2.56rem;
      left: -0.25rem; }
    .user-card__panel_green {
      background-color: #4eb7a8;
      color: white; }
      .user-card__panel_green::before {
        background-color: #4eb7a8; }
  .user-card__name {
    font-weight: normal;
    margin: 0;
    margin-top: 0.625rem;
    color: #e75735;
    font-size: 1.125rem; }
  .user-card__prof {
    margin: 0;
    text-transform: uppercase;
    color: #747474;
    font-size: 0.625rem;
    letter-spacing: 0.06rem; }
  .user-card__socials {
    margin: 0.625rem auto; }
    .user-card__socials i {
      font-size: 1.75rem;
      letter-spacing: 0.625rem;
      margin-left: 0.5rem;
      color: #747474; }
      .user-card__socials i.ion-logo-twitter {
        color: #e75735; }
  .user-card__name_green, .user-card__prof_green, .user-card__socials_green {
    color: white; }
    .user-card__name_green i, .user-card__prof_green i, .user-card__socials_green i {
      color: inherit; }
      .user-card__name_green i.ion-logo-twitter, .user-card__prof_green i.ion-logo-twitter, .user-card__socials_green i.ion-logo-twitter {
        color: inherit; }
