.map {
  width: 36.875rem;
  height: 16.875rem;
  border-radius: 0.3125rem;
  overflow: hidden; }
  .map__bottom {
    background-color: #e75735;
    color: white;
    width: 100%;
    height: 4.375rem;
    display: table;
    border-radius: 0 0 0.3125rem 0.3125rem;
    user-select: none; }
    .map__bottom:hover {
      cursor: default; }
  .map__slogan {
    display: table-cell;
    vertical-align: middle;
    font-weight: 300;
    font-size: 1.75rem;
    text-align: center;
    width: 10rem; }
  .map__address {
    display: table-cell;
    vertical-align: middle;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 0.75rem;
    letter-spacing: 0.06rem; }
  .map__address1, .map__address2 {
    display: block; }
  .map__point, .map__target {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    width: 3.44rem; }
    .map__point i, .map__target i {
      cursor: pointer; }
  .map__frame {
    height: 12.5rem; }
